/* Socail-media */

.social-media-wrap {
	/* float: left; */
	
	display: flex;
	flex-direction: column;
	row-gap: 50px;
	
	margin-top: 100px;
	width: 35px;	
}
.social-media {
	background-color: transparent;
	width: 35px;
	height: 35px;
	
	font-size: 25px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 3px;
}
.social-media__icon {
	color: var(--accent);
}
.social-media__icon:hover {
	opacity: 0.8;
}
.social-media:active{
	position: relative;
	top: 1px;
}

@media (max-width: 620px) {
	
	.social-media-wrap {
		row-gap: 50px;
		margin: 25px 0;
	}
}

@media (max-width: 400px) {
	
	.social-media-wrap{
		display: none;
	}
}