/* Footer */

.footer {
	margin-top: auto;
	padding: 70px;
	background-color: var(--footer-bg);
	color: var(--footer-text);
}
.footer-social-media {
	display: none;
}
.footer__wrapper {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
}

.copyright {
	font-size: 16px;
}

.copyright p + p {
	margin-top: 0.5em;
}
@media (max-width: 620px) {

	/* Footer */

	.footer {
		padding: 40px 0 30px;
	}

	.footer__wrapper {
		row-gap: 20px;
	}
}

@media (max-width: 400px) {
	.footer p {
		font-size: 12px;
	}
	.footer-social-media{
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		padding-bottom: 30px;
	}
}